body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-montserrat: Montserrat;
  --font-plus-jakarta-sans: "Plus Jakarta Sans";
  --font-rubik: Rubik;

  /* font sizes */
  --font-size-base: 16px;
  --font-size-xs: 12px;
  --font-size-xl: 20px;
  --font-size-66xl: 85px;
  --font-size-sm: 14px;
  --font-size-5xl: 24px;
  --font-size-29xl: 48px;
  --font-size-lg: 18px;

  /* Colors */
  --white: #fff;
  --orange: #ef790c;
  --color-darkorange-100: rgba(239, 121, 12, 0.05);
  --color-darkorange-200: rgba(239, 121, 12, 0.2);
  --dark: #606060;
  --black: #1a202c;
  --color-steelblue-100: #486284;
  --color-gainsboro: #dde0e4;

  /* Gaps */
  --gap-3xs: 10px;
  --gap-5xs: 8px;
  --gap-11xl: 30px;
  --gap-10xl: 29px;
  --gap-lg: 18px;
  --gap-46xl: 65px;
  --gap-31xl: 50px;
  --gap-2xl: 21px;
  --gap-8xs: 5px;
  --gap-lgi: 19px;
  --gap-21xl: 40px;
  --gap-xl: 20px;

  /* Paddings */
  --padding-8xs: 5px;
  --padding-31xl: 50px;
  --padding-3xs: 10px;
  --padding-xl: 20px;

  /* border radiuses */
  --br-12xs: 1px;
  --br-8xs: 5px;
  --br-16xl: 35px;
  --br-3xs: 10px;
  --br-21xl: 40px;
  --br-5xs: 8px;
}
